    /**
     * The size of the column, expressed as a fraction of 12 (e.g., 6 for half width).
     * Can also be "auto" for automatic sizing.
     */
    @Prop() size: string | undefined = undefined;

    render() {

        const sizeClass =
            this.size === 'auto'
                ? 'w-auto'
                : this.size
                    ? `w-${this.size}/12`
                    : 'w-full';

        return (
            <Host class={`bq-col ${sizeClass}`}>
                <slot></slot>
            </Host>
        );
    }
